.jarviswidget {
    margin: 0 0 30px;
    position: relative;
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    padding: 0
}

.widget-content-padding {
    padding: 20px
}

.widget-content-padding .well {
    margin-bottom: 0
}

.jarviswidget>header {
    height: 34px;
    padding: 0;
    line-height: 40px
}

.ui-sortable .jarviswidget-sortable>header {
    cursor: move;
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    line-height: normal;
    box-shadow: inset 0 -2px 0 rgba(0, 0, 0, .05);
    -moz-box-shadow: inset 0 -2px 0 rgba(0, 0, 0, .05);
    -webkit-box-shadow: inset 0 -2px 0 rgba(0, 0, 0, .05)
}

.jarviswidget>header>.widget-icon {
    display: block;
    width: 30px;
    height: 32px;
    position: relative;
    float: left;
    font-size: 111%;
    line-height: 32px;
    text-align: center;
    margin-right: -10px
}

.ui-sortable .jarviswidget-sortable.jarviswidget-collapsed>header {
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0
}

.jarviswidget>header h2 {
    height: 100%;
    width: auto;
    display: inline-block;
    font-size: 14px;
    position: relative;
    margin: 0;
    line-height: 34px;
    font-weight: 400;
    letter-spacing: 0
}

.jarviswidget>header>h2 {
    margin-left: 10px;
    float: left
}

.jarviswidget>header>:first-child.widget-icon {
    margin-left: 0
}

.jarviswidget>header .jarviswidget-icon {
    width: 16px;
    height: 16px;
    float: left;
    margin: 12px 10px 0 0;
    display: block;
    zoom: 1;
    z-index: 4000
}

.jarviswidget-ctrls {
    width: auto;
    float: right;
    padding: 0;
    margin: 0
}

.jarviswidget-ctrls .button-icon {
    min-width: 30px;
    height: 32px;
    float: left;
    position: relative;
    font-family: Arial, Helvetica, sans-serif;
    border-left: 1px solid rgba(0, 0, 0, .09)
}

.jarviswidget-ctrls .button-icon:hover {
    background-color: rgba(0, 0, 0, .05)
}

.jarviswidget-loader {
    width: 32px;
    height: 32px;
    margin: 0;
    float: right;
    background-repeat: no-repeat;
    background-position: center center;
    display: none;
    text-align: center;
    line-height: 32px;
    font-size: 111%
}

.jarviswidget>div {
    float: left;
    width: 100%;
    position: relative;
    font-size: 13px;
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    margin: 0;
    border-width: 1px 1px 2px;
    border-style: solid;
    border-top: 0;
    border-right-color: #CCC!important;
    border-bottom-color: #CCC!important;
    border-left-color: #CCC!important;
    padding: 13px 13px 0;
    overflow: visible
}

.jarviswidget .widget-body {
    min-height: 100px;
    position: relative;
    padding-bottom: 13px
}

.jarviswidget .widget-body.widget-hide-overflow {
    overflow: hidden
}

.widget-toolbar.no-border {
    border-left: 0
}

.widget-body.no-padding {
    margin: -13px -13px 0
}

.jarviswidget.well.transparent .widget-body.no-padding {
    margin: 0!important
}

.widget-body>table {
    margin-bottom: 0
}

.widget-body.no-padding .alert {
    margin: 0 0 10px;
    padding: 10px;
    box-shadow: none!important;
    -webkit-box-shadow: none!important;
    -moz-box-shadow: none!important;
    border-width: 0 0 1px!important;
    border-radius: 0!important;
    -webkit-border-radius: 0!important;
    -moz-border-radius: 0!important
}

.widget-body.no-padding .cke_chrome,
.no-padding .md-editor {
    border: 0
}

.widget-toolbar>.smart-form {
    margin-top: 2px
}

.widget-toolbar>.smart-form .toggle:last-child {
    font-size: 12px;
    line-height: 29px
}

.widget-toolbar>.smart-form .radio input+i,
.widget-toolbar>.smart-form .checkbox input+i,
.widget-toolbar>.smart-form .toggle input+i {
    border-width: 1px;
    border-color: #C7C7C7!important;
    margin-top: 1px;
    box-shadow: 0 1px 1px #FFF, 0 1px 1px #858585 inset;
    -webkit-box-shadow: 0 1px 1px #FFF, 0 1px 1px #858585 inset;
    -moz-box-shadow: 0 1px 1px #FFF, 0 1px 1px #858585 inset
}

.jarviswidget.well {
    margin: 0 0 30px
}

.jarviswidget.well>div {
    border: 0!important;
    box-shadow: none!important;
    -webkit-box-shadow: none!important;
    -moz-box-shadow: none!important
}

.jarviswidget.well header {
    display: none
}

.widget-footer {
    display: block;
    min-height: 32px;
    vertical-align: middle;
    position: relative;
    background-color: #F8F7F7;
    padding: 5px;
    border-top: 1px solid #E4E4E4;
    text-align: right;
    margin: 0 -13px -13px
}

.no-padding .widget-footer {
    margin: 0
}

.widget-footer.smart-form>label {
    margin-top: 4px;
    display: block
}

.jarviswidget-editbox {
    display: none;
    padding: 10px
}

.jarviswidget-timestamp {
    font-size: 12px;
    color: #868686;
    font-style: italic;
    margin: 10px 0 0
}

.jarviswidget-placeholder {
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0;
    margin-bottom: 28px;
    padding: 0;
    -webkit-box-sizing: border-box;
    -khtml-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box
}

body.nooverflow {
    overflow: hidden;
    position: fixed;
    width: 100%
}

#jarviswidget-fullscreen-mode {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999
}

#jarviswidget-fullscreen-mode .jarviswidget {
    margin: 0;
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0
}

#jarviswidget-fullscreen-mode .jarviswidget>div {
    overflow-y: scroll;
    -moz-border-radius: 0;
    -khtml-border-radius: 0;
    -webkit-border-radius: 0;
    border-radius: 0
}

#jarviswidget-fullscreen-mode>div>header {
    cursor: default
}

.widget-body-toolbar,
.smart-form.widget-body-toolbar {
    display: block;
    padding: 8px 10px;
    margin: -13px -13px 13px;
    min-height: 42px;
    border-bottom: 1px solid #ccc;
    background: #fafafa
}

.no-padding.widget-body-toolbar,
.no-padding .widget-body-toolbar,
.no-padding .smart-form.widget-body-toolbar {
    display: block;
    margin: 0
}

.widget-body-toolbar.smart-form .inline-group,
.smart-form .widget-body-toolbar .inline-group {
    float: left;
    margin-top: 4px
}

.widget-body-toolbar .btn {
    vertical-align: middle
}

.widget-body-toolbar .btn-xs {
    margin-top: 5px
}

.widget-body-ajax-loading::before {
    content: url(../img/ajax-loader.gif);
    padding-top: 18%;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    color: #fff;
    display: block;
    background: rgba(255, 255, 255, .4);
    height: 100%;
    z-index: 1;
    width: 100%;
    position: absolute
}

.widget-body-ajax-loading:hover {
    cursor: wait!important
}

