.color-select {
    list-style: none;
    margin: 0;
    padding: 4px;
    min-width: 166px;
    max-width: 156px;
    right: -3px
}

.color-select li {
    display: block;
    margin: 2px;
    float: left
}

.color-select li span,
.color-box {
    display: block;
    width: 22px;
    height: 22px;
    padding: 0;
    background: #333;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, .1);
    cursor: pointer
}

.color-select li span:hover {
    border: 3px solid rgba(0, 0, 0, .2)
}

.color-box {
    position: relative;
    vertical-align: middle;
    border: 1px solid rgba(255, 255, 255, .3)!important;
    outline: 1px solid rgba(0, 0, 0, .1);
    background: rgba(255, 255, 255, .3)!important
}

.color-box:hover,
.open .color-box {
    outline: 1px solid rgba(0, 0, 0, .1)!important
}

.color-box:hover {
    -webkit-transform: scale(1.07);
    -moz-transform: scale(1.07)
}

.color-box:active {
    top: 1px;
    left: 1px
}

.widget-toolbar>.color-box {
    margin-top: 7px;
    width: 18px;
    height: 18px;
    outline: 1px solid rgba(0, 0, 0, .2)
}

.widget-toolbar>.color-box:hover,
.widget-toolbar>.color-box:active,
.widget-toolbar>.color-box:focus {
    outline: 1px solid rgba(0, 0, 0, .25)!important
}

.widget-toolbar .dropdown-menu.color-select {
    right: -1px
}

.jarviswidget-ctrls a {
    text-decoration: none;
    font-size: 14px;
    text-align: center;
    display: inline-block;
    line-height: 32px;
    padding: 0;
    margin: 0;
    color: #333
}

.rtl .jarviswidget>header {
    padding: 0 7px 0 0
}

.rtl .jarviswidget>header h2 {
    float: right;
    text-align: right
}

.rtl .jarviswidget>header .jarviswidget-icon {
    float: right;
    margin: 12px 0 0 7px
}

.rtl .jarviswidget-ctrls {
    float: left;
    padding: 10px 0 0 3px;
    margin: 0
}

.rtl .jarviswidget-ctrls .button-icon {
    margin: 0 0 5px 5px
}

.rtl .jarviswidget-loader {
    float: left
}

.jarviswidget:before,
.jarviswidget:after,
.jarviswidget>div:before,
.jarviswidget>div:after,
.inner-spacer:before,
.inner-spacer:after,
.jarviswidget-editbox:before,
.jarviswidget-editbox:after,
.clearfix:before,
.clearfix:after {
    content: "\0020";
    display: block;
    height: 0;
    overflow: hidden;
    font-size: 0;
    line-height: 0;
    visibility: hidden
}

.jarviswidget:after,
.jarviswidget>div:after,
.inner-spacer:after,
.jarviswidget-editbox:after,
.clearfix:after {
    clear: both
}

.jarviswidget,
.jarviswidget>div,
.inner-spacer:after,
.jarviswidget-editbox,
.clearfix {
    zoom: 1
}

