.smart-form .input,
.smart-form .select,
.smart-form .textarea,
.smart-form .radio,
.smart-form .checkbox,
.smart-form .toggle,
.smart-form .button {
    position: relative;
    display: block;
    font-weight: 400
}

.smart-form .input input,
.smart-form .select select,
.smart-form .textarea textarea {
    display: block;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    width: 100%;
