    max-width: 100%;
    float: left;
    padding-right: 25px;
    padding-bottom: 13px
}

.superbox-img:hover {
    opacity: .8
}

.superbox-float {
    float: left
}

.superbox-close {
    opacity: .7;
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 15px;
    transition: all .1s linear 0s
}

.superbox-close:hover {
    opacity: 1
}

.superbox-imageinfo {
    display: inline-block;
    max-width: 500px;
    color: #fff;
    padding: 0 25px 0 0
}

.superbox-imageinfo span {
    font-size: 13px;
    color: #a19994;
